	changeSelectName(list, name, cusName) {
		if (list) {
			list.forEach((items, i) => {
				items.forEach((item, ind) => {
					if (item[name]) {
						item['selectNameOpt'] = item[name];
					}
					delete item[cusName];
				})
			})
		}
		// return list
	}

