	// 
	chooseItem(data) {
		let all = true;
		this.custExplTable.forEach(item => {
			if (data.label == item.label) {
				item.isCheck = !item.isCheck;
			}
		});
		if (data.isCheck) {
			this.PersonCheck.push(data);
			this.custExplTable.forEach(item => {
				all = item.isCheck && all
			});
		} else {
			this.PersonCheck.forEach((item, i) => {
				if (item.value == data.value) {
					this.PersonCheck.splice(i, 1);
					return;
				}
			})
			all = false;
		}
		this.PersonCheck.sort((a, b) => {
			return a.orderCode > b.orderCode
		})
		this.isAll = all;
		console.log(data)
	}
